@import url(https://fonts.googleapis.com/css?family=K2D|Montserrat);
body {
  background-color: rgb(34, 34, 32);
  background-position: cover;
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}
.App {
  text-align: center;
}

.color-nav-link {
	background-color: #E3B924;
}

.color-nav {
	width: 100%;
	margin: 0;
	background: rgba(7, 7, 6, 0.931);
	box-shadow: 1px -.5px 3px .5px rgb(122, 101, 9, 3);
}

.text-nav {
	font-family: 'K2D', sans-serif;
	font-size: 1.2em;
	font-weight: bold;
	color: rgb(250, 248, 248);
}

.mir {
	font-family: 'K2D', sans-serif;
	font-weight: bold;
	color: rgb(250, 248, 248);
	font-size: 1.6em;
	text-shadow:
		-2px 2px 0 #000,
		1px 4px 0 #000;
}

.size {
	height: 4em;
}

.navbar-fixed { z-index: 9999; }

.height-nav-side {
	height: 100vh;
}

.photo-mir-small {
	display: center;
	margin-top: 30px;
	width: 175px;
	height: 170px;
	border-radius: 50%;
}

.text-nav-side {
	font-family: 'K2D', sans-serif;
	font-size: 1.3em;
	/* font-weight: bold; */
	color: rgb(250, 248, 248);
}

.nav-movil {
	display: flex;
	justify-content:flex-start;
}
.nav-desk {
	display: flex;
	justify-content: flex-end;
}

@media screen and (max-width: 900px) {
	.nav-movil {
		width: 70vw !important;
		justify-content: center;
	}
}

@media screen and (max-width: 600px) {
	.text-nav {
		font-size: calc(1.3em - .3vmin);
	}
	.mir {
		font-size: calc(1.7em - .2vmin);
	}
	nav ul a {
		padding: 0px 5px !important;
	}
}

@media screen and (max-width:320px) {
	.mir {
		font-size: 1.3em;
	}
}
.aboutme {
	color: #FFFFFF;
	margin-top: 5em;
	height: 90vh;
	display: flex;
	align-items: center;
}

.text-body {
	font-family: 'Montserrat', sans-serif;
	font-size: calc(1em + 1vmin); 
	text-align: justify;
	display: flex;
	justify-content:space-evenly;
}
.contain-text-about {
	max-width: 55%;
}
.contain-img-about {
	display: flex;
	align-items: center;
}
.photo-mir {
	max-height: 50vh;
	border-radius: 15px;
	box-shadow: 2px 2px 3px 2px rgb(7 7 6 / 67%);
}
.mir-about {
	text-align: left;
}

.shadow {
	box-shadow: 1px -.5px 3px .5px rgb(7, 7, 6, 0.671);
}

@media screen and (max-width: 980px) {
	.text-body {
		font-size: calc(.9em + 1vmin);
	}

	.photo-mir {
		display: flex;
		height: 35vh;
	}
}

@media screen and (max-width: 774px) {
	.contain-text-about {
		max-width: 80%;
	}
	.text-body {
		font-size: calc(.9em + 1vmin);
		flex-direction: column-reverse;
		align-items: center;
	}
	.photo-mir {
		display: flex;
		height: 35vh;
	}
	.aboutme {
		height: auto;
	}
}

.background-home {
    background: url(/static/media/fondo.51c89902.png);
    background-size: cover;
    background-repeat: no-repeat;
    max-width: 100vw;
    height: 90vh;
}
.content-home {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 46vh;
}
.background-div {
    width: 55%;
    border-radius: 2em;
}


.photo-home {
    max-width: 35rem;
    height: auto;
    border-radius: 2em;
    display: flex;
    justify-content: center;
    margin: auto;
}

.text-home {
    max-width: 39%;
    font-family: 'K2D', sans-serif;
    font-weight: bold;
    color: rgb(250, 248, 248);
    text-shadow: -1px 1px 0 #000,
        1px 3px 0 #000;
}
.text-home h3 {
    font-size: 2.5rem;
}

@media screen and (min-width: 1240px) {
    .photo-home {
        max-width: 50rem;
    }
    .text-home h3 {
        font-size: 3.5rem;
    }
}

@media screen and (max-width: 900px) {
    .content-home {
        flex-direction: column-reverse;
    }
    .photo-home {
        margin-top: .5em;
        width: 90%;
    }
    .text-home {
        margin-top: .5em;
        max-width: 90%;
    }
    .background-div{
        margin: 2rem 0;
        width: 100%;
    }
}

@media screen and (max-width: 1200px) and (min-height: 1366px) {
    .photo-home{
        height: 200px;
    }
    .background-div,.text-home{
        margin-top: 30em;
    }
}

@media screen and (max-height: 466px) {
    .photo-home{
        height: 150px;
    }
    .background-home {
        height: 50vh !important;
    }
}
.projects {
	font-family: 'Montserrat', sans-serif;
	width: 100%;
	min-height: 92vh;
	height: auto;
}
.title-project{
	color: #ffffff;
	text-align: center;
	margin-top: 3em;
}

.content-cards {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
}
.card{
	max-width: 22%;
	color: #ffffff;
}
.card .card-image {
	background-color: #fff;
}
.card .card-image img {
	width: 100%;
	max-height: 200px;
	object-fit: contain;
}
.title-projects{
	font-size: 1.9em;
	margin-bottom: 1em;
	color: #ffffff;
}

.card-content {
	min-height: 13rem;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
}
.btn-content {
	display: flex;
	justify-content: space-evenly;
	width: 100%;
}
.btn-content button {
	width: 45%;
	font-size: 1.3rem;
}

.c-click-text {
	color: #ffffff !important;
	font-size: .7rem;
	font-style: oblique;
}
.c-title {
	font-size: 1.5em;
	font-weight: bold;
	margin: .5rem;
}

.tec {
	font-weight: bold;
}

.reve-card {
	font-size: 1.2em;
	background-color: rgb(58, 57, 55) !important;
}

@media screen and (max-width: 1096px) {
	.card {
		max-width: 40%;
	}
}
@media screen and (max-width: 600px) {
	.card {
		max-width: 90%;
	}
}
